body {
  font-family: Arial, serif;
  font-size: 16px;
  line-height: 18px;
}
body .header__nav .container .row .col-lg-1 ul li a {
  display: flex;
  align-items: center;
  position: relative;
  font: 0.875em/50px "Arial";
  color: #777a79;
  text-decoration: none;
  z-index: 10;
}
body .header__nav .container .row .col-lg-1 ul li ul {
  display: none;
  flex-direction: column;
  position: absolute;
  top: 40px;
  background: #fff;
  z-index: 10;
}
body .header__nav .container .row .col-lg-1 ul li ul li a {
  line-height: 30px;
}
body .header__nav .container .row .col-lg-1 ul li:hover ul {
  display: flex;
}
body .header__nav .container .row .col-lg-5 ul {
  display: flex;
  justify-content: space-between;
}
body .header__nav .container .row .col-lg-5 ul li a {
  display: flex;
  align-items: center;
  position: relative;
  font: 0.875em/50px "Arial";
  color: #777a79;
  text-decoration: none;
}
body .header__headline {
  height: 135px;
  background-color: #e6e6e6;
}
body .header__headline .container .row .col-lg-12 p {
  font: 8.9em/135px "BravoSC";
  color: #eaeaea;
  text-align: center;
  position: relative;
}
body .header__headline .container .row .col-lg-6 .logo {
  height: 135px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: -135px;
  z-index: 5;
}
body .header__headline .container .row .col-lg-6 .logo a {
  display: block;
  background-image: url("../img/logo.png");
  background-size: cover;
}
body .header__headline .container .row .col-lg-6 .logo div {
  position: relative;
  left: -15px;
}
body .header__headline .container .row .col-lg-6 .logo div p:nth-child(1) {
  margin: 0 0 0 20px;
  font: 1.188em/1em "BravoSC";
  color: #00548f;
  text-align: left;
}
body .header__headline .container .row .col-lg-6 .logo div p:nth-child(2) {
  font: 1.5em/1em "BravoSC";
  color: #00548f;
  text-align: left;
}
body .header__headline .container .row .col-lg-6 .search {
  width: 95%;
  height: 135px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: absolute;
  top: -135px;
  z-index: 5;
}
body .header__headline .container .row .col-lg-6 .search input {
  width: 460px;
  height: 33px;
  padding: 0 0 0 15px;
  font: 0.875em/33px "Arial";
  color: #9fa1a2;
  background-color: #f5f5f5;
  border: 1px solid #c4c4c4;
}
body .header__headline .container .row .col-lg-6 .search i {
  width: 37px;
  height: 33px;
  line-height: 33px;
  text-align: center;
  background: #fff;
  border-top: 1px solid #c4c4c4;
  border-right: 1px solid #c4c4c4;
  border-bottom: 1px solid #c4c4c4;
}
body .header__mainNav {
  background-color: #009fff;
}
body .header__mainNav .container .row .col-lg-12 ul {
  display: flex;
  justify-content: space-between;
}
body .header__mainNav .container .row .col-lg-12 ul li {
  position: relative;
}
body .header__mainNav .container .row .col-lg-12 ul li a {
  height: 60px;
  padding: 0 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  text-decoration: none;
}
body .header__mainNav .container .row .col-lg-12 ul li a span {
  display: block;
  text-align: center;
  text-transform: uppercase;
}
body .header__mainNav .container .row .col-lg-12 ul li a span:nth-child(1) {
  font: 0.938em/1.063em "Arial";
  font-weight: bold;
  color: #fff;
}
body .header__mainNav .container .row .col-lg-12 ul li a span:nth-child(2) {
  font: 0.938em/1.063em "Arial";
  color: #9fdbff;
}
body .header__mainNav .container .row .col-lg-12 ul li a:after {
  content: "";
  width: 100%;
  height: 0;
  position: absolute;
  top: 100%;
  left: 0;
  border: 15px solid transparent;
}
body .header__mainNav .container .row .col-lg-12 ul li a:hover {
  background-color: #007eec;
}
body .header__mainNav .container .row .col-lg-12 ul li a:hover:after {
  border: 90px solid transparent;
  border-top: 18px solid #007eec;
}
body .header__mainNav .container .row .col-lg-12 ul .active {
  background-color: #007eec;
}
body .header__mainNav .container .row .col-lg-12 ul .active:after {
  content: "";
  width: 100%;
  height: 0;
  position: absolute;
  top: 100%;
  left: 0;
  border: 90px solid transparent;
  border-top: 18px solid #007eec;
}
body #overlay {
  z-index: 150;
  position: fixed;
  background-color: #000;
  opacity: .7;
  filter: alpha(opacity=80);
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  cursor: pointer;
  display: none;
}
body .content .container .row .col-lg-3 .aside {
  margin: 0 0 30px;
}
body .content .container .row .col-lg-3 .aside__vhLogo {
  height: 105px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-image: url("../img/vh_logo.jpg");
  background-size: cover;
}
body .content .container .row .col-lg-3 .aside__vhLogo p {
  padding: 0 0 0 130px;
  font: 2.438em/0.875em "BravoSC";
  color: #d10000;
}
body .content .container .row .col-lg-3 .aside__vhLogo p:nth-child(1) {
  font: 1.625em/0.875em "BravoSC";
  color: #d10000;
}
body .content .container .row .col-lg-3 .aside__menu {
  margin: 30px 0;
}
body .content .container .row .col-lg-3 .aside__menu ul li a {
  padding: 0 0 0 20px;
  display: block;
  font: 0.938em/30px "Arial";
  font-weight: bold;
  color: #0081ef;
  text-decoration: none;
  border: 1px solid #c4c4c4;
}
body .content .container .row .col-lg-3 .aside__menu ul li a:hover {
