}
body .content .container .row .col-lg-3 .aside__bestHolo a h2,
body .content .container .row .col-lg-3 .aside__newHolo a h2,
body .content .container .row .col-lg-3 .aside__threeD a h2 {
  padding: 0 0 0 15px;
  font: 0.938em/40px "Arial";
  font-weight: bold;
  color: #777;
  text-transform: uppercase;
  background-color: #eee;
  border-bottom: 1px solid #c4c4c4;
}
body .content .container .row .col-lg-3 .aside__bestHolo a span,
body .content .container .row .col-lg-3 .aside__newHolo a span,
body .content .container .row .col-lg-3 .aside__threeD a span {
  display: block;
  padding: 20px 15px;
  font: 0.875em/1.25em "Arial";
  font-weight: bold;
  color: #0081ef;
  text-transform: uppercase;
}
body .content .container .row .col-lg-3 .aside__bestHolo a img,
body .content .container .row .col-lg-3 .aside__newHolo a img,
body .content .container .row .col-lg-3 .aside__threeD a img {
  width: 100%;
  padding: 15px;
}
body .content .container .row .col-lg-3 .aside__examples {
  margin: 30px 0;
  border: 1px solid #c4c4c4;
}
body .content .container .row .col-lg-3 .aside__examples h2 {
  padding: 0 0 0 15px;
  font: 0.938em/40px "Arial";
  font-weight: bold;
  color: #777;
  text-transform: uppercase;
  background-color: #eee;
  border-bottom: 1px solid #c4c4c4;
}
body .content .container .row .col-lg-3 .aside__examples ul li {
  display: flex;
  flex-wrap: wrap;
}
body .content .container .row .col-lg-3 .aside__examples ul li a {
  width: 50%;
  display: inline-block;
}
body .content .container .row .col-lg-3 .aside__examples ul li a img {
  width: 100%;
  padding: 10px;
}
body .content .container .row .col-lg-3 .aside__examples .controls {
  display: flex;
}
body .content .container .row .col-lg-3 .aside__examples .controls .prev,
body .content .container .row .col-lg-3 .aside__examples .controls .next {
  width: 50%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font: 0.875em/40px "Arial";
  color: #0081ef;
  text-align: center;
  background-color: #eee;
  border-top: 1px solid #c4c4c4;
}
body .content .container .row .col-lg-3 .aside__examples .controls .prev .dot,
body .content .container .row .col-lg-3 .aside__examples .controls .next .dot {
