}
body .content .container .row .col-lg-3 .aside__examples .controls .prev:hover,
body .content .container .row .col-lg-3 .aside__examples .controls .next:hover {
  cursor: pointer;
}
body .content .container .row .col-lg-3 .aside__custom {
  margin: 30px 0;
  border: 1px solid #c4c4c4;
}
body .content .container .row .col-lg-3 .aside__custom h2 {
  padding: 10px 0 10px 15px;
  font: 0.938em/1.25em "Arial";
  font-weight: bold !important;
  color: #0081ef;
  text-transform: uppercase;
  background-color: #eee;
  border-bottom: 1px solid #c4c4c4;
}
body .content .container .row .col-lg-3 .aside__custom p {
  padding: 10px 15px;
  font: 0.875em/1em "Arial";
  color: #777;
}
body .content .container .row .col-lg-3 .aside__custom p a {
  color: #0081ef;
}
